.colorWrapper {
    width: 28px;
    height: 28px;
    margin-right: 5px;
    margin-bottom: 5px;
    border-radius: 50%;
}

.colorWrapper:nth-child(4n) {
    margin-right: 0;
}

.colorWrapper:hover {
    border: 2px solid rgba(255, 255, 255, .87);
}

.outline {
    border: 2px solid rgba(255, 255, 255, .3);
}


