.headerTitle {
    display: inline-block;
    padding-left: 4px;
    font-size: 22px;
}

.leftNav {
    display: flex;
    align-items: center;

    width: 232px;
    padding-right: 30px;
}

.menuButton {
    width: 48px;
    height: 48px;
    padding: 12px;
    margin: 0 4px;

    border: none;
    border-radius: 50%;
    background-color: transparent;
    background-image: url('./../../img/burger.svg');
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer;
}

.menuButton:hover {
    background-color: rgba(255, 255, 255, .1);
}

.logo {
    margin-right: 4px;
}

@media (max-width: 810px) {
    .leftNav {
        width: 197px;
        padding-right: 5px;
    }
}

@media (max-width: 580px) {
    .headerTitle {
        display: none;
    }

    .logo {
        display: none;
    }

    .leftNav {
        width: 100px;
    }
}

@media (max-width: 500px) {
    .leftNav {
        width: 58px;
    }
}
