.searchBar {
    width: 100%;
    padding: 5px 49px 0 56px;
    font-size: 16px;
    font-weight: 500;
    color: rgba(255, 255, 255, .87);
    background-color: rgba(255, 255, 255, .2);
    border: none;
    border-radius: 9px;
}

.searchBarInput::placeholder {
    color: rgba(255, 255, 255, .87);
}

.searchBarWrapper {
    position: relative;
    display: flex;

    width: 720px;
    padding-left: 10px;
    border-radius: 9px;
}

.searchBarInput {
    width: 100%;
    padding: 11px 0;
    background-color: transparent;
    border: none;
    border-radius: 9px;
    font-size: 16px;
    font-weight: 500;
    color: rgba(255, 255, 255, .87);
    outline: none;
}

.searchBarButtonLeft {
    position: absolute;
    width: 42px;
    height: 42px;
    margin: 3px 3px 3px 8px;
    border: none;
    border-radius: 50%;
    background-color: transparent;
    background-image: url('./../../img/search.svg');
    background-position: center;
    background-repeat: no-repeat;
}

.searchBarButtonRight:hover {
    background-color: rgba(255, 255, 255, .2);
}


.searchBarButtonRight {
    position: absolute;
    right: 0;
    width: 42px;
    height: 42px;
    margin: 3px;
    border: none;
    border-radius: 50%;
    background-color: transparent;
    background-image: url('./../../img/cross.svg');
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer;
}

@media (max-width: 1030px) {
    .searchBarWrapper {
        width: 600px;
    }
}

@media (max-width: 930px) {
    .searchBarWrapper {
        width: 500px;
    }
}

@media (max-width: 810px) {
    .searchBarWrapper {
        width: 400px;
    }
}

@media (max-width: 670px) {
    .searchBarWrapper {
        width: 300px;
    }
}

@media (max-width: 450px) {
    .searchBarWrapper {
        width: 210px;
    }
}
