.homepageWrapper {
    padding: 0 150px;
    width: 100%;
}

.homepageTitle {
    min-width: 300px;
    margin: 0 auto;
    color: green;
    text-align: center;
    font-size: 50px;
}

.notesList {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: flex-start;
}

.notesListColumn {
    width: 600px;
    margin: 0 auto;
    flex-direction: column;
    justify-content: flex-start;
}

.notesWrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.listTitle {
    padding: 0 20px 0 0;
    margin: 0 0 12px 0;

    font-size: 22px;
    font-weight: 500;
    color: rgba(255, 255, 255, 0.4);
}

@media (max-width: 1150px) {
    .homepageWrapper {
        padding: 0;
    }
}

@media (max-width: 620px) {
    .notesListColumn {
        width: 300px;
    }
}
