.visuallyHidden {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;

    white-space: nowrap;


    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.picturesWrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
}

.addFormWrapper {
    width: 600px;
    padding: 3px 5px 3px 15px;
    margin: 0 auto 50px;
    background-color: transparent;
    border: 1px solid rgba(255, 255, 255, .2);
    border-radius: 7px;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.6), 0 2px 6px 2px rgba(0, 0, 0, 0.302);
    transition: 300ms;
}

.formInput {
    width: 100%;
    padding: 5px 0;
    height: 35px;

    background-color: transparent;
    color: #e8eaed;
    border: none;
    outline: none;
    font-size: 15px;
    resize: none;
    overflow-y:hidden;
}

.formInput::placeholder {
    color: rgba(255, 255, 255, .4);
}

.rightButtons {
    display: flex;
}

.formRow {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.formTitleInput {
    font-size: 17px;
}

.leftFooterWrapper {
    display: flex;
    margin-left: -8px;
}

.closeButton {
    padding: 8px 24px;
    font-size: 14px;
    font-weight: 500;
    color: rgba(255, 255, 255, .87);
    cursor: pointer;
}

.closeButton:hover {
    background-color: rgba(255, 255, 255, .05);
}

.addAuthorButton {
    width: 120px;
    margin-left: auto;
    padding: 8px 24px;
    font-size: 14px;
    font-weight: 500;
    color: rgba(255, 255, 255, .87);
    cursor: pointer;
}

.addAuthorButton:hover {
    background-color: rgba(255, 255, 255, .05);
}

.addTime,
.addAuthor,
.addColor,
.addPicture,
.addArchive,
.deleteButton,
.fastenButton,
.createListButton,
.addArtButton,
.addPictureButton {
    width: 36px;
    height: 36px;
    margin-right: 12px;
    background-size: 19px;
    border-radius: 50%;
    background-color: transparent;
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer;
    opacity: 0.7;
}

.addTime:hover,
.addAuthor:hover,
.addColor:hover,
.addPicture:hover,
.addArchive:hover,
.deleteButton:hover,
.createListButton:hover,
.addArtButton:hover,
.addPictureButton:hover {
    background-color: rgba(255, 255, 255, .1);
    opacity: 1;
}

.createListButton,
.addArtButton,
.addPictureButton {
    width: 50px;
    height: 50px;
    background-size: 21px;
    opacity: 0.5;
}

.addTime {
    background-image: url(data:image/svg+xml;base64,);
}

.addAuthor {
    background-image: url(data:image/svg+xml;base64,);
}

.addColor {
    position: relative;
    background-image: url(data:image/svg+xml;base64,);
}

.addColor:hover .colorList {
    opacity: 1;
    visibility: visible;
}

.addColor:not(:hover) .colorList {
    transition: opacity 0.7s, visibility 0s linear 0.7s;
}

.colorList {
    position: absolute;
    z-index: 900;
    top: 0;

    display: flex;
    flex-wrap: wrap;

    padding: 7px;
    margin-top: -110px;
    width: 141px;

    background-color: #202124;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    opacity: 0;
    visibility: hidden;
}

.addPicture {
    display: block;
    margin-right: 0;
    background-image: url(data:image/svg+xml;base64,);
}

.addArchive {
    background-image: url(data:image/svg+xml;base64,);
}

.createListButton {
    background-image: url(data:image/svg+xml;base64,);
}

.addArtButton {
    background-image: url(data:image/svg+xml;base64,);
}

.addPictureButton {
    display: block;
    background-image: url(data:image/svg+xml;base64,);
}

.deleteButton {
    background-image: url('./../../img/trash.svg');
}

.fastenButton {
    margin-right: 0;
    width: 34px;
    height: 34px;
    background-size: 25px;
    opacity: 0.85;
    background-image: url(data:image/svg+xml;base64,);
}

.fastenButton:hover {
    background-color: rgba(255, 255, 255, .2);
}

.addPictureWrapper {
    width: 36px;
    margin-right: 12px;
}

.fasted {
    background-image: url(data:image/svg+xml;base64,);
}

.addAuthorForm {
    display: none;
    padding: 8px;
    position: absolute;
    top: 35px;
    z-index: 200;
    width: 200px;
    background-color: #202124;
    opacity: 1;
    border: 2px solid rgba(255, 255, 255, .87);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.5);
}

.addAuthorFormShow {
    display: block;
}

.addAuthorTitle {
    padding: 0;
    margin: 0 0 8px 0;
    color: rgba(255, 255, 255, .87);
}

.addAuthorInput {
    width: 100%;
    padding: 5px 0;
    height: 35px;

    background-color: transparent;
    color: #e8eaed;
    border: none;
    outline: none;
    font-size: 15px;
    resize: none;
    overflow-y:hidden;
}

.addAuthorInput::placeholder {
    color: rgba(255, 255, 255, .4);
}

.authorWrapper {
    margin-left: auto;
    margin-bottom: 7px;
}

.authorTitle {
    margin-right: 5px;
    font-size: 14px;
    color: rgba(255, 255, 255, .5);
}

.authorResult {
    fonst-size: 16px;
    color: rgba(255, 255, 255, .87);
}

.addAuthorWrapper {
    position: relative;
    opacity: 1;
}

.authorWrapper {
    margin-left: auto;
    text-align: right;
    margin-bottom: 7px;
}

.addTimeWrapper {
    position: relative;
}

.addTimeForm {
    display: none;
    flex-direction: column;
    position: absolute;
    z-index: 200;
    padding: 10px;
    background-color: #202124;
    border: 2px solid rgba(255, 255, 255, .87);
}

.addTimeTitle {
    padding: 0;
    margin: 0 0 8px 0;
    color: rgba(255, 255, 255, .87);
}

.addTimeDateInput {
    width: 100%;
    padding: 5px 0;
    height: 35px;

    font-size: 20px;
    background-color: transparent;
    color: #e8eaed;
    border: none;
    outline: none;
    resize: none;
    overflow-y: hidden;
}

.addTimeText {
    padding: 0;
    margin: 0;
    font-size: 16px;
    color: rgba(255, 255, 255, .6);
}

.addTimeButton {
    width: 120px;
    margin: 0 auto;
    padding: 8px 24px;
    font-size: 14px;
    font-weight: 500;
    color: rgba(255, 255, 255, .87);
    cursor: pointer;
    background-color: transparent;
    border: none;
}

.addTimeButton:hover {
    background-color: rgba(255, 255, 255, .05);
}

.addTimeInput {
    padding: 5px 0;
    width: 50px;
    height: 35px;

    font-size: 20px;
    background-color: transparent;
    color: #e8eaed;
    border: none;
    outline: none;
}

.addTimeLabel {
    margin-bottom: 10px;
}

.addTimeInput::placeholder {
    color: rgba(255, 255, 255, .9);
}

.addTimeSpan {
    padding-left: 10px;
    color: rgba(255, 255, 255, .6);
}

.addTimeFormShown {
    display: flex;
}

.timeWrapper {
    margin-top: 10px;
    font-size: 15px;
    width: 270px;
    padding: 6px;
    border: 2px solid rgba(255, 255, 255, .6);
    border-radius: 20px;
    color: rgba(255, 255, 255, .6);
    cursor: pointer;
}

.timeText {
    font-weight: 500;
    color: rgba(255, 255, 255, .8);
}

.deleteTimeButton {
    width: 90px;
    padding: 8px;
    font-size: 14px;
    font-weight: 500;
    color: red;
    cursor: pointer;
    background-color: transparent;
    border: none;
    text-align: center;
}

.deleteTimeButton:hover {
    background-color: rgba(255, 255, 255, .05);
}

.buttonTimeWrapper {
    display: flex;
}

@media (max-width: 870px) {
    .addFormWrapper {
        width: 500px;
    }
}

@media (max-width: 805px) {
    .addFormWrapper {
        width: 400px;
    }
}

@media (max-width: 620px) {
    .addFormWrapper {
        width: 300px;
    }

    .formBottomRow {
        flex-direction: column;
    }
}
