.searchWrapper {
    padding: 0 150px;
    width: 100%;
}

.searchTitle {
    min-width: 300px;
    margin: 0 auto 50px;
    color: purple;
    text-align: center;
    font-size: 50px;
}

.notesList {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: flex-start;
}

.notesListColumn {
    width: 600px;
    margin: 0 auto;
    flex-direction: column;
    justify-content: flex-start;
}

.notFound {
    width: 300px;
    margin: 50px auto;
    font-size: 35px;
    font-weight: 500;
    color: red;
    text-align: center;
}

@media (max-width: 1150px) {
    .searchWrapper {
        padding: 0;
    }
}

@media (max-width: 620px) {
    .notesListColumn {
        width: 300px;
    }
}

