.leftMenuWrapper {
    display: flex;
    flex-direction: column;

    width: 325px;
    min-height: 92vh;
    margin-left: -325px;
}

.menuFooter {
    margin-top: auto;
    text-align: center;
    color: red;
}

.menuShow {
    margin-left: 0;
    animation-name: showMenu;
    animation-duration: 300ms;
}

.menuClose {
    animation-name: closeMenu;
    animation-duration: 300ms;
}

@keyframes showMenu {
    0% { margin-left: -325px; }
    100% { margin-left: 0; }
}

@keyframes closeMenu {
    0% { margin-left: 0; }
    100% { margin-left: -325px; }
}

.menuList {
    padding: 8px 0;
    margin: 0;
    border-bottom: 1px solid rgba(255, 255, 255, .2);
    list-style: none;
}

.menuItem {
    padding: 0;
    margin: 0;
    border-radius: 0 25px 25px 0;
}

.menuItem:hover {
    background-color: rgba(255, 255, 255, .2);
}

.menuItemText {
    display: inline-block;
    padding: 4px 0  4px 30px;
    width: 100%;

    line-height: 40px;
    color: rgba(255, 255, 255, .87);
}

.menuLink {
    display: flex;
    align-items: center;

    padding-left: 24px;
    text-decoration: none;
}

@media (max-width: 1200px) {
    .leftMenuWrapper {
        width: 250px;
        margin-left: -250px;
    }

    .menuShow {
        margin-left: 0;
        animation-name: showMenu;
        animation-duration: 300ms;
    }

    .menuClose {
        animation-name: closeMenu;
        animation-duration: 300ms;
    }

    @keyframes showMenu {
        0% { margin-left: -250px; }
        100% { margin-left: 0; }
    }

    @keyframes closeMenu {
        0% { margin-left: 0; }
        100% { margin-left: -250px; }
    }
}

@media (max-width: 620px) {
    .leftMenuWrapper {
        width: 100%;
        margin-left: -100%;
        position: absolute;
        background-color: rgba(0, 0, 0, .85);
        z-index: 900;
        min-height: 100vh;
    }

    .menuShow {
        margin-left: 0;
        animation-name: showMenu;
        animation-duration: 300ms;
    }

    .menuClose {
        animation-name: closeMenu;
        animation-duration: 300ms;
    }

    @keyframes showMenu {
        0% { margin-left: -100%; }
        100% { margin-left: 0; }
    }

    @keyframes closeMenu {
        0% { margin-left: 0; }
        100% { margin-left: -100%; }
    }

    .menuFooter {
        display: none;
    }

    .menuList {
        background-color: #202124;
    }
}
