.rightNavWrapper {
    display: flex;
    margin-right: 400px;
}

.layoutButton {
    width: 48px;
    height: 48px;
    padding: 12px;
    margin: 0 4px;

    border: none;
    border-radius: 50%;
    background-color: transparent;
    background-image: url(data:image/svg+xml;base64,);
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer;
    opacity: 0.87;
}

.layoutButton:hover {
    background-color: rgba(255, 255, 255, .1);
}

.columnStyle {
    background-image: url(data:image/svg+xml;base64,);
}

@media (max-width: 1500px) {
    .rightNavWrapper {
        margin-right: 200px;
    }
}

@media (max-width: 1300px) {
    .rightNavWrapper {
        margin-right: 100px;
    }
}

@media (max-width: 1200px) {
    .rightNavWrapper {
        margin-right: auto;
    }
}

@media (max-width: 658px) {
    .layoutButton {
        display: none;
    }
}
